import React from "react";
import DeleteOutlineIcon from "@mui/icons-material/DeleteOutline";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { removeItem, resetCard } from "../../redux/cardReducer";
import { loadStripe } from "@stripe/stripe-js";
import { makeRequest } from "../../makeRequest";
import { useSpring, animated } from "@react-spring/web";
import DoubleArrowIcon from "@mui/icons-material/DoubleArrow";

const ShoppingCard = ({ onClose }) => {
  const [props, api] = useSpring(
    () => ({
      from: { x: 400 },
      to: { x: 0 },
    }),
    []
  );

  const dispatch = useDispatch();
  const products = useSelector((state) => state.cart.products);

  const totalPrice = () => {
    let total = 0;
    products.forEach((item) => (total += item.quantity * item.price));
    return total.toFixed(2);
  };

  const stripePromise = loadStripe(
    "pk_live_51NQeDdF04NJVyUy6YkcPgzwtfeQfwysbrQMoXm0xalwq4TmUFmUeWH1qhlM7CGN1xEhDfzqCLs0G19bcPyz2JuBe00WD97bU8B"
  );
  const handlePayment = async () => {
    try {
      const stripe = await stripePromise;
      const res = await makeRequest.post("/orders", {
        products,
      });
      await stripe.redirectToCheckout({
        sessionId: res.data.stripeSession.id,
      });
    } catch (err) {
      console.log(err);
    }
  };

  return (
    <animated.div className="shopping_cart" style={props}>
      <button onClick={onClose}>
        <DoubleArrowIcon />
      </button>
      <h3>Your shopping cart</h3>

      {products.map((item) => (
        <div className="shopping-card-product">
          <img src={process.env.REACT_APP_UPLOAD_URL + item.img} alt="" />
          <div className="description">
            <h4>{item.title}</h4>
            <p>{item.desc.slice(0, 25) + "..."}</p>
            <p>
              {item.quantity} x {"£" + item.price}
            </p>
          </div>
          {console.log(item)}
          <div className="icon">
            <DeleteOutlineIcon
              style={{
                marginTop: "30px",
                marginLeft: "20px",

                color: "red",
                cursor: "pointer",
              }}
              onClick={() => dispatch(removeItem(item.id))}
            />
          </div>
        </div>
      ))}
      <div className="price">
        <h4>Subtotal</h4>
        <h4>£{totalPrice()}</h4>
      </div>
      <button onClick={handlePayment}>Proceed to checkout</button>
      <p
        style={{ cursor: "pointer", userSelect: "none", color: "red" }}
        onClick={() => dispatch(resetCard())}
      >
        Clear cart
      </p>
    </animated.div>
  );
};

export default ShoppingCard;
