import React from "react";
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart";
import FavoriteBorderIcon from "@mui/icons-material/FavoriteBorder";
import AddIcon from "@mui/icons-material/Add";
import useFetch from "../../hooks/useFetch";
import { useParams } from "react-router-dom";
import { useState } from "react";
import { useDispatch } from "react-redux";
import { addToCart } from "../../redux/cardReducer";
import { useSpring, animated } from "@react-spring/web";

const Product = () => {
  const [springs, api] = useSpring(() => ({
    from: { x: 0 },
  }));
  const handleClick = () => {
    api.start({
      from: {
        opacity: 0,
      },
      to: {
        opacity: 1,
      },
    });
  };

  const [quantity, setQuantity] = useState(1);

  const [selectedImg, setSelectedImg] = useState("img");

  const id = useParams().id;

  const dispatch = useDispatch();

  const { data, loading, error } = useFetch(`/products/${id}?populate=*`);

  return (
    <div className="single-product-page">
      <div className="images">
        <div className="thumbnails">
          <img
            src={
              process.env.REACT_APP_UPLOAD_URL +
              data?.attributes?.img?.data?.attributes?.url
            }
            alt=""
            onClick={() => {
              setSelectedImg("img");
              handleClick();
            }}
          />
          <img
            src={
              process.env.REACT_APP_UPLOAD_URL +
              data?.attributes?.img2?.data?.attributes?.url
            }
            alt=""
            onClick={() => {
              setSelectedImg("img2");
              handleClick();
            }}
          />
        </div>
        <div className="mainImg">
          <animated.img
            src={
              process.env.REACT_APP_UPLOAD_URL +
              data?.attributes[selectedImg]?.data?.attributes?.url
            }
            alt=""
            style={{ ...springs }}
          />
        </div>
      </div>

      <div className="product-details">
        <h4>{data?.attributes.title}</h4>
        <h4>{"£" + data?.attributes.price}</h4>
        <p>{data?.attributes.desc}</p>
        <div className="product-dimensions">
          <h5>Dimensions</h5>
          <h5>{data?.attributes.dimensions}</h5>
        </div>
        <div className="product-quantity">
          <AddIcon
            style={{ background: "#d9d9d9", cursor: "pointer" }}
            onClick={() => setQuantity((prev) => (prev === 1 ? 1 : prev - 1))}
          />
          {quantity}
          <AddIcon
            style={{ background: "#d9d9d9", cursor: "pointer" }}
            onClick={() => setQuantity((prev) => prev + 1)}
          />
        </div>
        <button
          className="product-add-to-card"
          onClick={() =>
            dispatch(
              addToCart({
                id: data.id,
                title: data.attributes.title,
                desc: data.attributes.desc,
                price: data.attributes.price,
                img: data.attributes.img.data.attributes.url,
                quantity,
              })
            )
          }
        >
          Add to card
          <ShoppingCartIcon style={{ margin: 5 }} />
        </button>
        <p className="add-to-wishlist">
          <FavoriteBorderIcon /> Add to wishlist
        </p>
      </div>
    </div>
  );
};

export default Product;
